import React from "react";

const Tutor = () => {
  return <div className="service service__Tutor">
    <div className="service__header">
      <div className="service__header__icon">
        <i className="im im-language" />
      </div>

      <div className="service__header__description">
        <h2>Tutor</h2>
        <p>I can also teach indivuduals or small groups various aspects of programming principles and webdesign.</p>
      </div>
    </div>

    <div className="service__me">
      <h3>I can share my knowledge of</h3>
      <ul>
        <li>Basic and advanced programming principles</li>
        <li>OOP and SOLID philosophies</li>
        <li>HTML5, CSS3 with SASS</li>
        <li>Javascript ES6</li>
        <li>Node.js, NPM and Express</li>
        <li>React and Redux</li>
        <li>C# and VB.NET</li>
        <li>Database modeling and SQL code</li>
        <li>Ms Office software (Excel, Word, Powerpoint, Access)</li>
        <li>Visual Basic for Applications (VBA)</li>
        <li>Source control (TFS, GIT)</li>
      </ul>
    </div>
  </div>;
};

export default Tutor;


